.introArticle {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: var(--light-text);
  background-image: url("../../assets/images/background-intro.jpeg");
  background-size: cover;
  background-position: center;
  font-weight: 900;

  .buttonDownload {
    width: fit-content;
    padding: 0.5rem 1rem;
    font-weight: bold;
    font-size: inherit;
  }

  .nextLink {
    padding-top: 1rem;
  }

  .helloSection {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
  }

  .jump {
    animation: jump 1.5s cubic-bezier(0.39, 0.575, 0.565, 1);
  }

  @keyframes jump {
    0% {
      transform: translateY(0);
    }

    50% {
      transform: translateY(0.5rem);
    }

    100% {
      transform: translateY(0);
    }
  }
}

@media (hover: hover) and (any-hover: hover) {
  .buttonDownload:hover {
    transform: scaleX(1.1);
    transition: ease-out 0.25s;
  }

  .nextLink:hover {
    transform: translateY(-1rem);
    transition: ease-in-out 0.25s;
  }
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 2.75rem;
  }
}
